}

.main-nav__list {
  margin: 0;
  color: inherit;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

@media (max-width: 1365px) {
  .main-nav__inner-wrapper--closed .main-nav__inner-mobile-wrapper {
    display: none;
  }
}

@media (max-width: 1365px) {
  .main-nav__inner-wrapper {
    grid-column: 1/-1;
    grid-row: 2/-1;
  }
}

@media (max-width: 1365px) {
  .main-nav__inner-mobile-wrapper {
    position: relative;
    width: 100%;
    align-self: end;
    top: 100%;
