}

.main-nav__toggle {
  display: none;
  position: relative;
  width: 24px;
  height: 12px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (max-width: 1365px) {
  .main-nav__toggle {
    display: block;
    grid-column: 1/2;
    grid-row: 1/2;
  }
}

.main-nav__inner-wrapper--closed ~ .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #122947;
  box-shadow: 0 6px 0 0 #122947, 0 12px 0 0 #122947;
}

.main-nav__inner-wrapper--closed ~ .main-nav__toggle:hover {
  opacity: 0.6;
}

.main-nav__inner-wrapper--closed ~ .main-nav__toggle:active {
  opacity: 0.3;
}

.main-nav__inner-wrapper--opened ~ .main-nav__toggle {
  width: 18px;
  height: 18px;
}
.main-nav__inner-wrapper--opened ~ .main-nav__toggle::before, .main-nav__inner-wrapper--opened ~ .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 18px;
  height: 2px;
  background-color: #122947;
}
.main-nav__inner-wrapper--opened ~ .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav__inner-wrapper--opened ~ .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav__inner-wrapper--opened ~ .main-nav__toggle:hover {
  opacity: 0.6;
}

.main-nav__inner-wrapper--opened ~ .main-nav__toggle:active {
  opacity: 0.3;
}

