  .card__type {
    margin-bottom: 15px;
  }
}

.card__description {
  margin: 0;
  font-size: 16px;
  line-height: 23px;
  margin-bottom: 5px;
}
.card__description--last {
  margin-bottom: 45px;
}
@media (max-width: 767px) {
  .card__description--last {
    margin-bottom: 40px;
  }
}

.card__details-wrapper {
  border-top: 1px solid #dbdce0;
  padding-top: 18px;
}
.card__details-wrapper:last-child {
  border-bottom: 1px solid #dbdce0;
  margin-bottom: 45px;
}

.card__subtitle {
  margin: 0;
  position: relative;
  font-size: 18px;
  line-height: 26px;
  padding-bottom: 20px;
  cursor: pointer;
}
.card__subtitle--volume {
  padding: 0;
}
.card__subtitle--closed::before, .card__subtitle--closed::after {
  content: "";
  position: absolute;
  top: 30%;
  transform: translateY(-50%);
  right: 0;
  width: 12px;
  height: 1px;
  background-color: #122947;
}
.card__subtitle--closed::after {
  transform: rotate(90deg);
}
.card__subtitle--opened::after {
  content: "";
  position: absolute;
  top: 30%;
  transform: translateY(-50%), rotate(90deg);
  right: 0;
  width: 12px;
  height: 1px;
  background-color: #122947;
}

.card__details-button {
  margin: 0;
}

.card__details-info {
  margin: 0;
  padding-bottom: 20px;
  font-size: 14px;
  line-height: 20px;
}

.card__subtitle--closed + .card__details-info {
  display: none;
}

.card__subtitle--opened + .card__details-info {
  display: block;
}

.card__volume {
  display: flex;
  border: none;
  padding: 0;
  margin-bottom: 45px;
}

.card__filter-list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.card__filter-item {
  margin-left: 15px;
}

.card__filter-control {
  position: relative;
  padding-left: 22px;
}

.card__filter-mark {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  box-sizing: border-box;
  width: 14px;
  height: 14px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
}

.card__filter-input:hover + .card__filter-mark {
  border-color: #122947;
}

.card__filter-input:focus + .card__filter-mark {
  box-shadow: 0 0 0 2px #122947;
}

.card__filter-input:checked + .card__filter-mark::before {
  content: "";
  position: absolute;
  display: block;
  box-sizing: border-box;
  top: 50%;
  left: 50%;
  background-color: #122947;
  transform: translateY(-50%) translateX(-50%);
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.card__filter-label {
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
}
@media (max-width: 767px) {
  .card__filter-label {
    font-size: 16px;
    line-height: 23px;
  }
}

.card__filter-wrapper {
  display: flex;
  align-items: center;
}

.card__filter-price {
  display: block;
  margin-right: 50px;
  font-weight: 500;
  font-size: 26px;
  line-height: 38px;
}